.skillsWrapper {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  background-color: #1a1614;
  margin: 1em;
  border-radius: 0.5em;
}

.skills {
	display: flex;
	flex-wrap: wrap;
	align-content: center;
	justify-content: center;
	padding: 20px 50px;
	background-color: #1a1614;
	margin: 1em;
	border-radius: 0.5em;
}

.skillCard {
  background-color: #fefffe;
  border-radius: 0.5em;
  margin: 1em 0;
  padding: 0.5em;
}

.hex {
  width: 4em;
  height: 4em;
  margin: 0 0.2em;
  transition: transform 0.5s, opacity 0.3s;
}

.hex:hover {
  transform: scale(1.2);
}

.skillsWrapper h3 {
  color: #fefffe;
  margin: 0;
  padding-top: 1em;
}

.skillsWrapper h4 {
  margin: 0;
  padding: 0.5em;
}
