@import url(https://fonts.googleapis.com/css?family=Fira+Mono:400,500,700);
@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
* {
  font-family: 'Fira Mono', monospace;
}

.App {
  text-align: center;
}

.cardWrapper {
  flex: 1 1;
}

.container {
  display: flex;
  padding: 0.2em;
  margin: 0;
  background-image: url(/portfolio-react-app/static/media/board.f530682c.gif);
  background-attachment: fixed;
  background-size: cover;
  background-repeat: repeat;
}

@media screen and (max-width: 959px) {
  .container {
    flex-direction: column;
  }
}

.aside > p {
  font-family: 'Open Sans', sans-serif;
  font-size: 17px;
  text-align: left;
  padding: 0 2em;
}

.headshot {
  margin-left: 0.6em;
  border-radius: 2em;
  min-width: 15em;
  height: 15em;
  width: 18em;
  align-self: center;
}

.card {
  display: flex;
  margin: 1em;
  background-color: #fefffe;
  padding: 1.3em;
  border-radius: 0.5em;
  margin: 1em;
}

.dev {
  display: flex;
  align-items: center;
  margin-left: 1.8em;
}

@media screen and (max-width: 959px) {
  .aside > p {
    font-size: 16px;
    padding: 0 0.8em;
  }
  .dev {
    align-content: center;
  }
  .headshot {
    align-self: center;
    height: 17em;
    width: 17em;
    min-width: 17em;
    margin-left: 0;
  }
  #devCard {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
  }
}

.contact {
  display: flex;
  justify-content: space-around;
}

.contact > span {
  flex: 1 1;
  margin: 1em 0;
}

span > p {
  font-size: 10px;
}

.devContact {
  color: black;
  -webkit-transition: opacity 0.3s, -webkit-transform 0.2s;
  transition: opacity 0.3s, -webkit-transform 0.2s;
  transition: transform 0.2s, opacity 0.3s;
  transition: transform 0.2s, opacity 0.3s, -webkit-transform 0.2s;
}

.devContact:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

@media screen and (max-width: 959px) {
  .contact > span {
    flex-wrap: wrap;
    margin: 0.3em;
  }

  span > p {
    display: none;
  }
}

.skillsWrapper {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  background-color: #1a1614;
  margin: 1em;
  border-radius: 0.5em;
}

.skills {
	display: flex;
	flex-wrap: wrap;
	align-content: center;
	justify-content: center;
	padding: 20px 50px;
	background-color: #1a1614;
	margin: 1em;
	border-radius: 0.5em;
}

.skillCard {
  background-color: #fefffe;
  border-radius: 0.5em;
  margin: 1em 0;
  padding: 0.5em;
}

.hex {
  width: 4em;
  height: 4em;
  margin: 0 0.2em;
  -webkit-transition: opacity 0.3s, -webkit-transform 0.5s;
  transition: opacity 0.3s, -webkit-transform 0.5s;
  transition: transform 0.5s, opacity 0.3s;
  transition: transform 0.5s, opacity 0.3s, -webkit-transform 0.5s;
}

.hex:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.skillsWrapper h3 {
  color: #fefffe;
  margin: 0;
  padding-top: 1em;
}

.skillsWrapper h4 {
  margin: 0;
  padding: 0.5em;
}

.Projects {
  background-color: #1a1614;
  margin: 1em 1em 1em 0;
  border-radius: 0.5em;
  color: #fefffe;
  text-align: center;
  height: 42em;
}

.projectImg {
  width: 7em;
  height: 7em;
  -webkit-transition: opacity 0.3s, -webkit-transform 0.5s;
  transition: opacity 0.3s, -webkit-transform 0.5s;
  transition: transform 0.5s, opacity 0.3s;
  transition: transform 0.5s, opacity 0.3s, -webkit-transform 0.5s;
}

.react {
  width: 10em;
  height: 10em;
}

.iconWrap {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 1em;
  height: 9em;
  width: 9em;
  background-color: #fefffe;
  margin: 0.8em 0.5em;
}

.projectImg:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.Projects > h3 {
  margin: 0.6em 0.5em;
}

@media screen and (width: 1330px to 959px) {
  .Projects {
    width: 7.5em;
    height: 30em;
  }

  .iconWrap {
    height: 6em;
    width: 6em;
  }

  .projectImg {
    width: 4.5em;
    height: 4.5em;
  }
  .react {
    width: 8em;
    height: 8em;
  }
}

@media screen and (max-width: 959px) {
  .Projects > h3 {
    margin: 0;
    padding: 1.2em 0 0.5em 0;
  }

  .Projects {
    margin: 0 1em 1em 1em;
    height: auto;
  }

  .projectsWrapper {
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
  }
}

.title > h1,
h2 {
  color: #fefffe;
  margin: 0.1em;
}

.title {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding-bottom: 1em;
}

.landing {
  background-image: url(/portfolio-react-app/static/media/backdrop2.0.aa1d072a.png);
  background-position: center;
  background-size: cover;
  height: 100vh;
}

.active {
  opacity: 0.5;
}

.GalleryNav {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-content: center;
  background-color: #1a1614;
  border-radius: 0.5em 0.5em 0 0;
  margin: 0 1em 0 1em;
}

.navImg {
  height: 2em;
  width: 2em;
  padding: 0.8em;
  margin-top: 1em;
  margin: 1.1em;
  background-color: #fefefd;
  border-radius: 0.5em;
}

.navImg:hover {
  opacity: 0.5;
}

.navReact {
  height: 3.5em;
  width: 3.5em;
  padding: 0.1em;
  margin-top: 1.1em;
  background-color: #fefefd;
  border-radius: 0.5em;
}

.navReact:hover {
  opacity: 0.5;
}

.arrow {
  padding: 1em 0;
  color: #fefefd;
}

.arrow:hover {
  color: black;
}

@media screen and (max-width: 959px) {
  .GalleryNav {
    align-items: center;
    justify-content: space-around;
  }

  .navImg {
    height: 1.5em;
    width: 1.5em;
    padding: 0.5em;
    margin: 0.5em;
  }

  .navReact {
    height: 2.6em;
    width: 2.6em;
    padding: 0em;
    margin-bottom: 0.95em;
  }

  .arrow {
    display: none;
  }
}

.content {
  display: flex;
  background-color: #fefffe;
  padding: 1.3em;
  border-radius: 0 0 0.5em 0.5em;
  margin: 0 1em 1em 1em;
  flex-wrap: wrap;
}

@media screen and (max-width: 959px) {
  .content {
    justify-content: center;
  }
}

.itemWrapper {
  display: flex;
  flex-wrap: wrap;
}

.item {
  height: 12em;
  width: 9em;
  margin: 0.85em;
}

.item > h5 {
  margin: 0.5em 0;
}

.item > p {
  font-size: 11px;
}

.itemImg {
  width: 4em;
  height: 4em;
  -webkit-transition: -webkit-transform 0.3s;
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}

.itemImg:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

@media screen and (max-width: 959px) {
  .itemWrapper {
    justify-content: center;
  }
}

